<template>
    <section class="user-details container pages" v-if="user">
        <h2>User Details: {{ user.username }}</h2>
        <!-- <pre>{{ user }}</pre> -->
        <ul v-if="reviews" class="reviews clean-list">
            <h2>Reviews</h2>
            <li
                class="flex column"
                v-for="(review, idx) in reviews"
                :key="review._id"
            >
                <span>Toy: {{ review.aboutToy.name }}</span>
                <span>{{ review.content }}</span>
                <star-rating
                    read-only
                    class="star-rating"
                    :increment="0.5"
                    :max-rating="5"
                    inactive-color="white"
                    active-color="gold"
                    :star-size="15"
                    :border-width="1"
                    v-model="review.rating"
                    border-color="#999"
                >
                </star-rating>
                <span>{{ reviewsWithDate[idx] }}</span>
            </li>
        </ul>
    </section>
</template>

<script>
import { userService } from "../services/user.service";
import StarRating from "vue-star-rating";

export default {
    data() {
        return {
            user: null,
        };
    },
    computed: {
        reviews() {
            return this.$store.getters.reviews;
        },
        reviewsWithDate() {
            return this.reviews.map((review) =>
                moment(review.createdAt).format("MMM Do YY")
            );
        },
    },
    async created() {
        const id = this.$route.params.id;
        const user = await userService.getById(id);
        this.user = user;
        console.log('this.user:', this.user);
        this.$store.dispatch({
            type: "loadReviews",
            filterBy: {byUser: this.user._id}
        });
    },
    components: {
        StarRating,
    },
};
</script>