<template>
    <div id="app">
        <header class="app-header flex space-between align-center mb30">
            <div>
                <h1 @click="goTo">Mister Toy</h1>
                <span v-if="user">Hello {{ user.username }}</span>
            </div>
            <div class="nav">
                <router-link v-if="!user" to="/login">Login</router-link> 
                <span v-else @click="logout">Log Out</span > |
                <router-link to="/">Toys</router-link> |
                <router-link to="/reviews">Reviews</router-link> |
                <span v-if="user && !user.isAdmin" @click="goToUserProfile">Profile</span>
                <router-link v-if="user && user.isAdmin" to="/admin"
                    >Admin</router-link
                >
                
            </div>
        </header>
        <router-view class="mb50" />
        <footer class="app-footer">
            <small> 2020 Yuval Beit On - All Rights Reserved</small>
        </footer>
    </div>
</template>


<script>
export default {
    data() {
        return {
            // loggedInUser: null,
        };
    },
    methods: {
        goTo() {
            if (this.$route.path === "/") return;
            this.$router.push("/");
        },
        goToUserProfile() {
            this.$router.push(`/userdetails/${this.user._id}`);
        },
        logout() {
            this.$store.dispatch({ type: "logout" });
        },
    },
    computed: {
        user() {
            return this.$store.getters.loggedinUser;
        },
    },
    created() {
        this.$store.dispatch({
            type: "loadToys",
        });
    },
};
</script>

