<template>
    <section class="all-reviews pages container">
        <h2>Reviews</h2>
        <ul v-if="reviews">
            <li
                v-for="(review, idx) in reviews"
                :key="review._id"
                class="flex column"
            >
                <span>Toy: {{ review.aboutToy.name }}</span>
                <span>By: {{ review.byUser.username }}</span>
                <span>Content: {{ review.content }}</span>
                <span>{{ reviewsWithDate[idx] }}</span>
                <star-rating
                    read-only
                    class="star-rating"
                    :increment="0.5"
                    :max-rating="5"
                    inactive-color="white"
                    active-color="gold"
                    :star-size="20"
                    :border-width="1"
                    v-model="review.rating"
                    border-color="#999"
                >
                </star-rating>
            </li>
        </ul>
    </section>
</template>

<script>
import StarRating from "vue-star-rating";

export default {
    computed: {
        reviews() {
            return this.$store.getters.reviews;
        },
        reviewsWithDate() {
            return this.reviews.map((review) =>
                moment(review.createdAt).format("MMM Do YY")
            );
        },
    },
    created() {
        this.$store.dispatch({
            type: "loadReviews"
        });
    },
    components: {
        StarRating,
    },
};
</script>

